 
.about__container
{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
  
}

.about__me
{
    width: 100%;
 
    border-radius: 3rem 3rem 0rem 0rem;
    background: linear-gradient(
        45deg, 
        transparent, 
        #0762b73d
        ,
         transparent
         );

    display: grid;
    place-items: center;
}

.about__MeImage
{
    border-radius: 3rem 3rem 0rem 0rem;
    overflow: hidden;
    transform: rotate(4deg);
    transition: var(--transition);

}

.about__MeImage:hover
{
    transform:rotate(0);
}

.about__cards
{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card
{
    background:var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover
{
    background:transparent;
    border-color: var(--color-primary-variant);
    cursor: default;

}

.about__icon
{
 
    font-size: 1.7rem;
    margin-bottom: 1.2rem;
}

.about__card h5
{
    font-size: 1rem;
}

.about__card small
{
    font-size: 0.7rem;
    color: var(--color-primary);

}

.about__content p
{
    margin: 2rem 0 2.6rem;
     
}

/*tablet*/
@media screen and (max-width:1024px)
{
   
    .about__container
    {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me
    {
        width: 70%;
        margin: 2rem auto 4rem;
    }

    .about__content p
    {
        margin:1rem 0 1.5rem;
    }
}


/*mob*/
@media screen and (max-width: 600px) {

    .about__me
    {
        width: 50%;
        margin: 0 auto 3rem;
        place-items:center;
    
    }
    .about__cards
    {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content
    {
        text-align: center;
    }

    .about__content p
    {
        margin:1.5rem 0;
    }
  }