@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,100;0,300;0,400;0,600;1,100;1,300;1,400;1,600&display=swap');

*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root
{
--color-bg: #353353;
--color-bg-variant:#352f8e;
--color-primary:#eeeeee;
--color-primary-variant:#FFD15A;
--color-white:#fff;
--color-light:rgb(254, 204, 204);

--transition: all 400ms ease;

--container-width-lg:75%;
--container-width-md:86%;
--container-width-sm:91%;

}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body
{
    font-family: 'Noto Sans', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(../src/assets/bg-texture.png);
}

.container{
    width: var(--container-width-lg);
    margin: auto;
}

h1,
h2,
h3,
h4,
h5
{

    font-weight: 400;

}

h1{
    font-size: 2.5rem;
}

section
{
    margin-top: 8rem;
    

}

section > h2, 
section > h5
{
text-align: center;
color: var(--color-light);
}

section > h2
{
    color: var(--color-light);
}

.text-light
{
    color: var(--color-light);
}

a
{
    color: var(--color-primary);
    transition:var(--transition);
}

a:hover
{
    color: var(--color-light);
}

.btn
{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover
{
    background: var(--color-primary);
    color: var(--color-bg);
    border-color: transparent;
}
.btn-primary
{
    background: var(--color-primary);
    color: var(--color-bg);
}

.btn-primary:hover
{
    background: transparent;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}
.img
{
    display: block;
    width: 100%;
    object-fit: cover;

}

/* tablet*/
@media screen and (max-width:1024) {
    .container
    {
        width: var(--container-width-md);
     
    }

    .section
    {
        margin-top: 6rem;

    }
}

/* mobile*/
@media screen and (max-width:600) {
    .container
    {
        width: var(--container-width-sm);
      
    }

    .section > h2
    {
        margin-top: 2rem;
   
        
    }
}